.Button {
   height: 50px;
   border-radius: 8px;
   font-weight: 500;
   background: #835af3;
   color: #fff;
   padding: 0 32px;

   display: flex;
   justify-content: center;
   align-items: center;

   cursor: pointer;
   border: 0;

   transition: filter 0.2s;

   img {
     margin-right: 8px;
    }

    &.outlined {
      background: #7d5eee;
      border: 1px solid #7d5eee;
      color:#ffffff;
    }

   &:not(:disabled):hover {
     filter: brightness(0.9);
    }
   &:disabled {
     opacity: 0.6;
     cursor: not-allowed;
    }
  }
